}
.product-details__price-cart {
  display: -ms-flexbox;
  display: flex;
  padding-top: 30px;
}
.product-details__price-cart p {
  -ms-flex-positive: 2;
      flex-grow: 2;
  font-size: 20px;
  font-weight: bold;
}



