
.product {
  background: #FFF;
  margin-bottom: 30px;
  position: relative;
  overflow: hidden;
}
.product .product__description,
.product .product__action {
  transition: 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) transform;
}
.product:hover .product__description {
  transform: translateY(-40px);
}
.product:hover .product__action {
  transform: none;
}
.product-link {
  display: block;
  color: #3D3D3D;
}
.product-link:hover {
  color: #3D3D3D;
  text-decoration: none;
}
.product .product__image {
  width: 100%;
}
.product__image img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 225px;
}
.product__description {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  padding: 20px 20px 15px;
  background: #FFF;
}
.product__info {
  -ms-flex: 2;
      flex: 2;
}
.product__description small {
  color: #808080;
}
.product__description h4 {
  margin: 3px 0 5px 0;
}
.product__price-cart {
  -ms-flex: 1;
      flex: 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: #51D2B7;
}
.product__price-cart p {
  -ms-flex-positive: 2;
      flex-grow: 2;
  font-size: 20px;
  font-weight: bold;
}
.product__action {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  transform: translateY(100%);
}
.product__action button {
  border-radius: 0;
  width: 100%;
  display: block;
}



